"use client";

import { useState, useEffect } from "react";
import { supabase } from "@/lib/supabase";
import { 
  Sparkles, ArrowUpRight, ArrowDownLeft, Wallet, Clock, 
  ChevronLeft, ShoppingBag, Lock, ShieldCheck, 
  Loader2, Phone, ArrowRight, Eye, EyeOff, LogOut 
} from "lucide-react";
import Link from "next/link";

export default function WalletPage() {
  const [phoneNumber, setPhoneNumber] = useState("");
  const [walletData, setWalletData] = useState<any>(null);
  const [transactions, setTransactions] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  
  const [walletState, setWalletState] = useState<'identify' | 'setup' | 'locked' | 'unlocked' | 'empty'>('identify');
  
  const [pin, setPin] = useState("");
  const [confirmPin, setConfirmPin] = useState("");
  const [pinError, setPinError] = useState("");
  const [showPin, setShowPin] = useState(false);

  useEffect(() => {
    const savedPhone = localStorage.getItem('storelink_user_phone');
    if (savedPhone) {
      setPhoneNumber(savedPhone);
      fetchWalletStatus(savedPhone);
    } else {
      setLoading(false);
      setWalletState('identify');
    }
  }, []);

  const fetchWalletStatus = async (userPhone: string) => {
    setLoading(true);
    setPinError("");
    let raw = userPhone.replace(/\D/g, ''); 
    let formatA = raw; 
    let formatB = raw.startsWith('0') ? '234' + raw.substring(1) : '0' + raw.substring(3);

    const { data, error } = await supabase
      .from('user_wallets')
      .select('*')
      .or(`phone_number.eq.${formatA},phone_number.eq.${formatB}`) 
      .maybeSingle();

    if (data) {
      setWalletData(data);
      setPhoneNumber(data.phone_number); 
      localStorage.setItem('storelink_user_phone', data.phone_number); 
      setWalletState(!data.wallet_pin ? 'setup' : 'locked');
    } else {
      setWalletState('empty');
    }
    setLoading(false);
  };

  const handleSetPin = async () => {
    if (pin.length !== 4 || pin !== confirmPin) {
      setPinError(pin.length !== 4 ? "PIN must be 4 digits." : "PINs do not match.");
      return;
    }
    setLoading(true);
    const { error } = await supabase
      .from('user_wallets')
      .update({ wallet_pin: pin })
      .eq('phone_number', phoneNumber);

    if (!error) unlockAndFetchHistory();
    else {
      setPinError("Failed to secure vault.");
      setLoading(false);
    }
  };

  const handleVerifyPin = () => {
    if (pin === walletData.wallet_pin) unlockAndFetchHistory();
    else { setPinError("Access Denied: Incorrect PIN."); setPin(""); }
  };

  const unlockAndFetchHistory = async () => {
    setLoading(true);
    const { data: history } = await supabase
      .from('coin_transactions')
      .select('*')
      .eq('phone', phoneNumber) 
      .order('created_at', { ascending: false });

    setTransactions(history || []);
    setWalletState('unlocked');
    setLoading(false);
  };

  if (loading) return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-white">
      <Loader2 className="animate-spin text-emerald-600 mb-4" size={40} />
      <p className="font-black text-gray-900 tracking-tighter uppercase">Decrypting Vault...</p>
    </div>
  );

  return (
    <div className="min-h-screen bg-[#FAFAFA] font-sans flex flex-col">
      {/* ðŸŸ¢ Audited Responsive Header */}
      <header className="fixed top-0 left-0 right-0 z-[100] bg-white/80 backdrop-blur-xl border-b border-gray-100">
        <div className="max-w-7xl mx-auto px-6 h-20 flex items-center justify-between">
          <div className="flex items-center gap-6">
            <Link href="/" className="p-2 hover:bg-gray-100 rounded-full transition"><ChevronLeft size={24} /></Link>
            <div className="h-8 w-[1px] bg-gray-200 hidden md:block" />
            <h1 className="text-xl font-black tracking-tighter uppercase italic text-gray-900">Empire Wallet</h1>
          </div>
          {walletState === 'unlocked' && (
            <button onClick={() => {localStorage.clear(); window.location.reload();}} className="flex items-center gap-2 text-xs font-bold text-red-500 bg-red-50 px-4 py-2 rounded-xl">
              <LogOut size={16} /> <span className="hidden md:inline">Sign Out</span>
            </button>
          )}
        </div>
      </header>

      {/* Spacer to push content below fixed header */}
      <div className="h-20" />

      <main className="flex-1 max-w-6xl mx-auto w-full p-6 md:p-12">
        <div className="max-w-4xl mx-auto">
          
          {/* PHASE 1: IDENTIFY */}
          {walletState === 'identify' && (
            <div className="bg-white p-10 md:p-16 rounded-[50px] shadow-sm border border-gray-100 text-center space-y-8 animate-in fade-in slide-in-from-bottom-10">
              <div className="w-20 h-20 bg-emerald-50 text-emerald-600 rounded-3xl flex items-center justify-center mx-auto"><Phone size={40} /></div>
              <div>
                <h2 className="text-4xl font-black tracking-tight">Access Your Assets</h2>
                <p className="text-gray-500 mt-2 font-medium">Verify your phone number to unlock your Empire Wallet.</p>
              </div>
              <div className="max-w-sm mx-auto space-y-4">
                <input type="tel" placeholder="080... or 234..." className="w-full p-5 bg-gray-50 rounded-3xl border-none outline-none focus:ring-2 ring-emerald-500 font-bold text-lg text-center" value={phoneNumber} onChange={(e) => setPhoneNumber(e.target.value)} />
                <button onClick={() => fetchWalletStatus(phoneNumber)} className="w-full bg-gray-900 text-white py-5 rounded-3xl font-bold shadow-xl flex items-center justify-center gap-2 hover:bg-black transition-all active:scale-95">Verify Identity <ArrowRight size={20} /></button>
              </div>
            </div>
          )}

          {/* PHASE 5: UNLOCKED (The Master View) */}
          {walletState === 'unlocked' && (
            <div className="grid grid-cols-1 md:grid-cols-12 gap-8 md:gap-12 items-start">
              
              {/* Left Column: Balance Card */}
              <div className="md:col-span-5 space-y-6 md:sticky md:top-32">
                <div className="bg-gray-900 rounded-[50px] p-10 text-white shadow-2xl relative overflow-hidden border-t border-white/10">
                  <div className="absolute top-[-20%] right-[-20%] w-64 h-64 rounded-full bg-emerald-500/10 blur-3xl" />
                  <div className="relative z-10">
                    <div className="flex justify-between items-start mb-12">
                      <div className="p-4 bg-white/10 rounded-2xl backdrop-blur-md"><Wallet className="text-emerald-400" size={28} /></div>
                      <Sparkles className="text-emerald-500" size={24} />
                    </div>
                    <span className="text-[10px] font-black opacity-40 tracking-[0.4em] uppercase">Coin Balance</span>
                    <div className="flex items-baseline gap-2 mt-2">
                      <span className="text-6xl font-black tracking-tighter">â‚¦{walletData.coin_balance?.toLocaleString() || "0"}</span>
                    </div>
                    <div className="mt-12 pt-8 border-t border-white/5">
                       <p className="text-[10px] font-bold text-white/30 uppercase tracking-[0.2em]">Verified Empire Citizen</p>
                    </div>
                  </div>
                </div>
              </div>

              {/* Right Column: Ledger */}
              <div className="md:col-span-7 space-y-6">
                <h3 className="text-[10px] font-black text-gray-400 tracking-[0.4em] uppercase flex items-center gap-3 px-2">
                  <Clock size={16} className="text-emerald-600" /> Transaction Ledger
                </h3>
                <div className="space-y-4">
                  {transactions.length > 0 ? transactions.map((tx) => (
                    <div key={tx.id} className="bg-white p-7 rounded-[40px] border border-gray-100 flex items-center justify-between shadow-sm transition-all hover:shadow-md">
                      <div className="flex items-center gap-6">
                        <div className={`p-4 rounded-2xl ${tx.type === 'spend' ? 'bg-red-50 text-red-500' : 'bg-emerald-50 text-emerald-600'}`}>
                          {tx.type === 'spend' ? <ArrowUpRight size={24} /> : <ArrowDownLeft size={24} />}
                        </div>
                        <div>
                          <p className="font-black text-gray-900 text-base leading-none">{tx.description}</p>
                          <p className="text-[10px] text-gray-400 font-bold mt-2 uppercase tracking-tight">
                            {new Date(tx.created_at).toLocaleDateString('en-GB', { day: '2-digit', month: 'short', year: 'numeric' })}
                          </p>
                        </div>
                      </div>
                      <span className={`font-black text-xl ${tx.type === 'spend' ? 'text-gray-900' : 'text-emerald-600'}`}>
                        {tx.type === 'spend' ? '-' : '+'}â‚¦{tx.amount.toLocaleString()}
                      </span>
                    </div>
                  )) : (
                    <div className="text-center py-20 bg-white rounded-[40px] border border-dashed border-gray-200">
                       <p className="text-gray-400 text-xs font-black uppercase tracking-widest">Vault activity empty</p>
                    </div>
                  )}
                </div>
              </div>
            </div>
          )}

          {/* ... Phase 2 (Setup), Phase 3 (Locked), Phase 4 (Empty) UI logic follows similar Desktop container style ... */}
          {walletState === 'locked' && (
             <div className="max-w-md mx-auto bg-white p-12 rounded-[50px] shadow-xl text-center space-y-8">
                <div className="w-20 h-20 bg-gray-100 text-gray-900 rounded-full flex items-center justify-center mx-auto"><Lock size={32} /></div>
                <h2 className="text-3xl font-black">Identity Check</h2>
                <input type="password" maxLength={4} autoFocus className="w-full p-6 bg-gray-50 rounded-3xl text-center text-6xl font-black tracking-[0.5em] outline-none" value={pin} onChange={(e) => setPin(e.target.value)} />
                <button onClick={handleVerifyPin} className="w-full bg-gray-900 text-white py-5 rounded-3xl font-bold">Unlock Assets</button>
             </div>
          )}
        </div>
      </main>

      {/* ðŸŒ‘ Soft Audited Footer */}
      <footer className="mt-auto bg-white border-t border-gray-100 py-16">
        <div className="max-w-6xl mx-auto px-12 flex flex-col md:flex-row justify-between items-center gap-8">
          <div className="flex items-center gap-2">
            <div className="w-8 h-8 bg-gray-900 rounded-lg" />
            <span className="font-black text-sm tracking-tighter uppercase">StoreLink Ecosystem</span>
          </div>
          <p className="text-xs font-bold text-gray-400 uppercase tracking-widest">Â© 2025 All Rights Reserved</p>
        </div>
      </footer>
    </div>
  );
}